<template>
	<div class="header-box">
        <slot name="first"><router-link tag="div" to='/home/Search'><p  class="iconfont icon-prev"></p></router-link></slot>
        <slot name="mid"><router-link to="/Location"><p style="margin-left:-0.01rem">{{this.$route.query.name}}1</p></router-link></slot>
        <slot name="last"><router-link tag="div" to='/Login' v-if="this.$store.state.ma==''"><p>登录/注册</p></router-link>
        <router-link tag="div" to='/home/mine' v-if="this.$store.state.ma!=''"><i class="iconfont icon-rentou"></i></router-link>
        </slot>
        <!-- <p v-show=""></p> -->
	</div>
</template>
<script>
	 export default {
    }
</script>
<style lang='scss' scoped>
.header-box{
    width: 100%;height:0.8rem;background: #2F90E8;display: flex;z-index: 100;
    justify-content: space-around;align-items: center;position: fixed;top:0;font-size:0.3rem;
    p{
        color: white;font-size: 0.35rem;margin-left: -0.5rem;
        
    }
    
    i{
        color: white;font-size: 0.4rem;
    }
    .span1{
        color: white;font-size: 0.35rem;
    }
    .logo{
        display: block;font-size: 0.25rem;text-decoration: none;
    }
}
</style>


